import React from "react";
import styled from "styled-components";
import { Menu } from "@styled-icons/material-sharp/Menu";

const StyledSearchBox = styled.button`
  width: 48px;
  height: 48px;

  border-radius: 50%;

  background-color: ${(props) => props.theme.color.ultramarineBlue};

  transition: opacity 150ms ease-in;
  cursor: pointer;

  &:hover {
    opacity: 0.4;
  }
`;

export default function SearchBox(): JSX.Element {
  return <StyledSearchBox></StyledSearchBox>;
}
